.natural-gallery-js .figure {
  left: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  float: left;
  overflow: hidden;
  margin: 0;
  transition: 1s background-color; }
  .natural-gallery-js .figure:hover .title.hover, .natural-gallery-js .figure:hover .title.hover, .natural-gallery-js .figure:hover .selectBtn {
    transform: translateY(0%); }
  .natural-gallery-js .figure.zoomable, .natural-gallery-js .figure .zoomable {
    cursor: zoom-in; }
  .natural-gallery-js .figure.activable, .natural-gallery-js .figure .activable {
    cursor: pointer; }
  .natural-gallery-js .figure.loaded > .image {
    opacity: 1;
    transition: 1s ease-out opacity, 1s ease-out transform, .2s ease-out margin; }
  .natural-gallery-js .figure.selected .selectBtn {
    transform: translateY(0%);
    top: 0;
    left: 0; }
  .natural-gallery-js .figure.zoomable:hover .image,
  .natural-gallery-js .figure .image.zoomable:hover {
    transform: rotate(1deg) scale(1.2); }
  .natural-gallery-js .figure .image {
    display: block;
    position: relative;
    margin: 0;
    width: auto;
    height: auto;
    flex: 1;
    z-index: 100;
    opacity: 0;
    background-repeat: no-repeat;
    background-position: center center;
    transition: .2s ease all; }
    .natural-gallery-js .figure .image.cover {
      background-size: cover; }
  .natural-gallery-js .figure .selectBtn {
    position: absolute;
    z-index: 991;
    top: 0;
    left: 0;
    padding: 10px;
    width: 48px;
    height: 48px;
    transition: .2s ease all;
    transform: translateY(calc(-100% - 15px));
    color: rgba(255, 255, 255, 0.9);
    fill: currentColor;
    cursor: pointer;
    background: rgba(0, 0, 0, 0.6); }
    .natural-gallery-js .figure .selectBtn svg {
      width: 28px; }
  .natural-gallery-js .figure .title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1100;
    background: rgba(0, 0, 0, 0.6);
    text-align: center;
    color: white;
    font-size: 14px;
    line-height: 1.7em;
    font-weight: normal;
    padding: 10px;
    transition: .2s ease all;
    overflow: hidden;
    white-space: normal; }
    .natural-gallery-js .figure .title.hover {
      padding: 10px;
      transform: translateY(calc(100% + 12px)); }
  .natural-gallery-js .figure .link {
    border: none;
    text-decoration: none;
    cursor: pointer; }
  .natural-gallery-js .figure .button {
    display: block;
    margin: 10px;
    padding: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
    border-radius: 4px; }

.pswp {
  z-index: 9999999; }
  .pswp .pswp__caption__center {
    text-align: center; }

.natural-gallery-js {
  position: relative;
  /**
    * Columns
    * ===================================================
    */
  /**
