  .natural-gallery-js .column {
    float: left; }
    .natural-gallery-js .column:last-child {
      margin-right: 0 !important; }
    .natural-gallery-js .column .figure:last-child {
      margin-bottom: 0 !important; }
  .natural-gallery-js .natural-gallery-body {
    padding: 0;
    margin: 0; }
    .natural-gallery-js .natural-gallery-body.resizing .figure, .natural-gallery-js .natural-gallery-body.resizing .column {
